.container {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #1d1f20;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.text {
  margin-top: 5rem;
  font-size: 1rem;
  font-family: 'Open Sans', 'Helvetica Neue';
  color: whitesmoke;
  text-align: center;
}
.spinner {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  float: left;
  background: rgb(231, 0, 0);
  transition: all 0.2s;
  animation: r5 1s 0s ease-out infinite;
}
@keyframes r5 {
  0% {
    box-shadow: 0 0 8px 6px rgb(231, 0, 0), 0 0 0px 0px transparent,
      0 0 0px 0px rgb(231, 0, 0);
  }
  10% {
    box-shadow: 0 0 8px 6px rgb(231, 0, 0), 0 0 12px 10px transparent,
      0 0 12px 14px rgb(231, 0, 0);
  }
  100% {
    box-shadow: 0 0 8px 6px rgba(26, 140, 255, 0), 0 0 0px 40px transparent,
      0 0 0px 100px rgba(26, 140, 255, 0);
  }
}
